.app {
  padding: 16px;
  min-height: 100vh;
  width: 100vw;

  background-image: url("../public/images/background.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &__wrapper {
    // padding: 12px 24px;
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: #f4d2e5;

    opacity: 0.9;
  }

  &__menu {
    padding: 12px 24px 0;
    width: 100%;
    margin-bottom: 12px;

    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__logo {
    font-size: 1.5rem;
    font-weight: 900;
  }
}

.nav {
  width: 100%;

  display: flex;
  justify-content: center;

  &__list {
    margin-top: 24px;
    width: 100%;
    list-style: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 8px;
  }

  &__item {
    padding: 8px;
    width: 40%;

    font-size: 1.1rem;
    text-align: center;
    cursor: pointer;

    &:last-of-type {
      border: 1px solid #603570;
      border-radius: 16px;
    }
  }

  &__link {
    text-decoration: none;
    color: inherit
  }
}